.registerCointainer{
    display: grid;
    grid-template-columns: 20% 60% 20%;
    height: 110vh;
    width: 100vw;
    background-color: #c10d11;
    background-image: linear-gradient(149deg, #c10d11 0%, #3174bd 46%, #d8a03b 100%);
    
}

.registerBox{
    padding-left: 3rem;
    display: grid;
    grid-area: 1/2/2/3;
    height: 100vh;
    width: 50vw;
    background-color: aliceblue;
    margin-top: 2vh;
    border-radius: 2rem;
    row-gap: .8rem;
    background-image: url("./images/Buy.jpg");
    background-size: cover;
    border: .09rem solid wheat;
    box-shadow: .1rem .3rem .5rem .1rem  rgb(231, 136, 2);
}

.label{
color: rgb(218, 116, 14);
text-shadow: .2rem .1rem .3rem  wheat;
font-stretch: expanded;
font-size: 1.5rem;
font-family: Georgia, 'Times New Roman', Times, serif;


}
.input{
    width: 30vw;
    height: auto;
    border-radius: .8rem;
    border: 0.3rem solid wheat;
    box-shadow: .2rem .2rem .2rem wheat;
    background-color: transparent;
}

.registerButton{

    width: 10rem;
    margin-top: 1.2rem;
    border-radius: 1.2rem;
    background-color: wheat;
    border: .2rem .2rem .2rem rgb(174, 171, 171);
}


@media (max-width:998px) {


.registerCointainer{
    background-image: url("./images/Buy.jpg");
    background-size: cover;
}
.registerBox{
    background-image: none;
    background-color: transparent;
}

html{
    font-size: 80%;
}
   
}




@media (max-width:768px) {
    html{
        font-size: 65%;
    }
    
}

@media (max-width:425px) {
    html{
        font-size: 40%;
    }
    
}