
.portcointaner{
    
    background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}
.card{
display: flex;
flex-direction: row;
column-gap: 2rem;
justify-content: center;
align-items: center;

background: rgb(62,138,149);
background: linear-gradient(103deg, rgba(62,138,149,0.7818098098321669) 16%, rgba(49,174,178,0.4379612293905264) 76%);
    height: 6rem;
    width: 95vw;
    border-radius: 2rem 2rem 2rem 2rem;
    box-shadow: 2px 5px 5px 2px rgb(79, 73, 72);
    margin: .6rem .6rem;

}


    .card h1{
        color: white;
        font-size: 18px;
        row-gap: 2px;
        padding-left: 10px;
    }



    
   
    .sellButton{
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        font-size: 1rem;
        border-radius: 1rem;
        box-shadow: .1rem .1rem .1rem .1rem rgb(40, 37, 32);
        height: 3rem;
        width: 7rem;
        text-decoration: none;
        background: rgb(33,118,130);
background: linear-gradient(103deg, rgba(33,118,130,0.9900117025766463) 12%, rgb(61, 147, 152) 93%);
    }

    .sellButton:hover{
   
        height: 3.2rem;
        width: 7.2rem;
        background-color: rgb(2, 8, 9);

    }