.cointainer{

    display: grid;
    grid-template-rows: 10vh 65vh 40vh 65vh 40vh 10vh;
    height: auto;
    background-color: #8EC5FC;
background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);

    margin:0;
    border-radius: 10px;
   /* background: rgb(67,30,161);
  background: linear-gradient(103deg, rgba(67,30,161,1) 0%, rgba(61,38,190,1) 100%); */
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  
  .homeBox{
  
    position: absolute;
    display: grid;
    justify-content: start;
    margin-left: 20px;
    grid-template-columns: 70% 30%;
    grid-area: 2/1/3/2;
    margin-top: 60px;
    border-style: solid 3px black;
    height: 60vh;
    width: 93vw;
    border-radius: 25px 0 25px 25px ;
    background-image: url("./images/OIP.png");
    background-size: cover;
   /* background: rgb(161,133,231);
    background: linear-gradient(103deg, rgba(161,133,231,1) 5%, rgba(214,206,232,1) 75%); */
  box-shadow: 7px 6px 5px rgb(178, 175, 175);
  }
  .upbutton{
  display: flex;
  align-content: center;
  margin-left: 10rem;
  margin-top: 4rem;
  color: wheat;
  font-size: 1.3rem;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 14rem;
  height: 4rem;
  border-radius: .8rem .5rem .5rem .4rem;
  background: rgb(73,31,181);
  background: linear-gradient(103deg, rgba(73,31,181,0.25184135557349174) 9%, rgba(139,109,204,1) 91%);

  }
  
  .homeBox-2{
    border-radius: 1.3rem;
    border: .3rem solid rgb(235, 235, 238);
    background-size: cover;
    
  
  }
  
  .circle{
   background-image: url("./images/flower.jpg");
   background-size: cover;
    position: relative;
    height: 25vh;
   margin-top: 1.3rem;
    border-radius: 50%;
    width: 9vw;
    padding-left: 2.5rem;
    margin-left: 5.5rem;
    border: 1rem solid whitesmoke;
    animation: change 10s alternate 0 infinite ease-in-out fill-mode;
  
  
  }
  
  
  @keyframes change {
  
    from{
  rotate: 0deg;
    }
    to{
    rotate:360deg;
    }
   
  }
  
  
  
  .second{
    display: flex;
   
    row-gap: 2.5rem;
    flex-direction: row;
    position: relative;
    grid-area: 3/1/4/2;
  
    font-size: 1rem;
    
  }
  .second h1{
    text-align: center;
    padding-right: 5rem;
    padding-top: 1rem;
  }
  .header{
    display: grid;
    grid-template-columns: 1.2rem 19rem 2rem 2rem 2rem 2rem 2rem;
    width: 95vw;
    text-align: start;
    flex-direction: row;
    justify-items: end;
    flex-wrap: nowrap;
    row-gap: 1.3rem;
    column-gap:  6.2rem;
    align-items: start;
    justify-items: start;
    flex-flow: nowrap;
    text-align: center;
  margin-top: .8rem;
    
    grid-area: 1/1/2/2;
    background: rgb(73,31,181);
  background: linear-gradient(103deg, rgba(73,31,181,0.25184135557349174) 9%, rgba(139,109,204,1) 91%);
  border-radius: 1.3rem;
  box-shadow: .7rem  rgb(220, 215, 215);
  
  }
  
  .header h1{
    display: flex;
    display: inline-block;
    white-space: pre-wrap;
    overflow: hidden;
    text-align: start;
    align-items: center;
    color: white;
    margin-bottom: .7rem;
    justify-content: end;
    justify-items: center;
    align-content: center;
    font-size: small;
  }
  
  .second h2{
    font-size: .7rem;
    color: white;
  }
  
  
  .box-3{
    grid-area: 4/1/5/2;
    background-color:rgb(205, 192, 235);
    border-radius: 1.3rem;
    border: .2rem solid rgb(235, 235, 238);
   background-size: 73rem 25rem;
   background-repeat: no-repeat;
    height: 30vh;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    column-gap: .5rem;
    
  
  }
  .box-3 h1{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.5rem;
  }
  
  .box-3 h2{
    color: wheat;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.5rem;
    text-align: bottom;
    }
  
    .box4{
      grid-area: 5/1/6/2;
     background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%); 
   
     border-radius: 2rem 1.2rem .6rem 0rem;
  
    }
   .circleR{
     background-image: url("./images/i3.png");
     background-size: cover;
    border-radius: 50%;
   border: .4rem .4rem 0 0 solid gray;
  
    height: 30vh;
    width: 20vw;
    animation: 5s linear 0s infinite  alternate running slidein;
    position: relative;
    box-shadow: 2.5rem solid gray;
   
   }
    
   @keyframes slidein {
    from{
      top: 0;
      left: 0;
    }
    to{
      top: 0;
  left: 75vw;
    }
    
   }
   .footer{
     grid-area: 6/1/7/2;
  margin: .6rem .6rem .6rem .6rem;
  background: rgb(70, 70, 74);
  background: linear-gradient(103deg, rgba(45, 45, 57, 0.782) 0%, rgba(37, 37, 37, 0.921) 100%);
   }