.itemsCointainer{
    display: grid;
    width: 70rem;
    background-color: #8EC5FC;
    background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
    
    
    }
    
    .itemBox{

        height: 10vh;
        width: 68rem;
        margin-left: 1vw;
        border: .2rem   solid white;
        margin-top: .4rem;
        border-width: .2rem 0px .2rem 0;
        display: grid;
        grid-template-columns: 5% 5% 15% 15% 25% 15% 20% ;
        background: rgb(122,188,207);
    background: linear-gradient(103deg, rgba(122,188,207,0.31808741235582605) 23%, rgba(201,241,236,0.510516434437845) 93%);
      
        
    }
    .name:hover{

        color: red;
    }

    .itemBox:hover{
        background: rgb(3, 29, 36);
        background: linear-gradient(103deg, rgba(122,188,207,0.31808741235582605) 23%, rgba(201,241,236,0.510516434437845) 93%);
          

    }
    .head {
        display: grid;
    padding-left: 5rem;
        width: 100vw;
        text-align: center;
        grid-template-columns: 10% 15% 15% 25% 15% 20% ;
        background-color: #E0C3FC;
    
        grid-auto-flow: row;
        color: white;
        font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
        font-size: .5rem;
        padding-left: 1.2rem;

      }

    
    .itemBox h1{
        display: grid;
        grid-auto-flow: column;
        grid-area: auto;
        text-align: top;
        align-items: center;
        font-size: 1.2rem;
        justify-content: center;
        text-transform: capitalize;
        color: rgb(4, 4, 5);
        font-size: 1rem;
        text-decoration-line: none;
    
    }