.bodye {
  display: flex;
  align-items: center;
  column-gap: 3rem;
  font-size: .9rem;
  text-decoration: none;
  justify-items: center;
 text-align: center;
}
.a {
  display: flex;
  color: black;
  font-size: .8rem;
  text-align: center;
  align-content: center;
  align-items: center;
}

.navbar {
  color: white;
  display: grid;
  grid-template-columns: 20% 50% 30%;
  grid-template-rows: 8vh;
  background: rgb(203,83,197);
background: linear-gradient(103deg, rgba(203,83,197,0.9584659612517251) 22%, rgba(218,138,209,0.510516434437845) 93%);
  border-radius: .8rem;
  text-decoration: none;
  width: auto;
  height: 3rem;
}


.homes {
  background-color: #ffe63b;
  font-size: .5rem;
  text-decoration: none;
}

.logo {
  grid-area: 1/1/2/2;
  font-size: 1.1rem;
  padding-left: .5rem;
  color: black;
}

.ab{
  grid-area: 1/1/2/2;
  width: 5rem;
  font-size: 1.2rem;
  padding-left: 4.8rem;
  background-color: black;
  height: 2rem;
  width: 5rem;
 align-content: center;
  margin-bottom: 4.8rem;
  align-items: center;
  color: black;
  background-color: transparent;
  text-align: end;
}
.end{
  grid-area:1 /3 /2 /4 ;
  display: grid;
  grid-template-columns: 40% 60%;

}


.a:hover {
  color: red;
  background-color: red;
  height: .2rem;

 
}
.wallet{
text-align: end;
padding-right: .8rem;
padding-bottom: .8rem;
color: gray;
font-size: 1rem;
}
.ab:hover {
  background-color: black;
  height: 2rem;
  color: white;
  font-size: 1.2rem;
  text-align: center;
  padding-top: .3rem;
  align-items: center;
  margin-top: .2rem;
  width: 8rem;
  border-radius: .5rem;
  margin-right: -.6rem;
  margin-bottom: .2rem;
  padding-bottom: 5rem;

}


